<template>
    <div class="content-table">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
                <router-link to="/category/add">
                <el-button icon="el-icon-plus" style="float: right; padding: 3px 0" type="text">Add Category</el-button>
                </router-link>
            </div>

        <div class="center">
            <vs-table>
                <template #thead>
                <vs-tr>
                    <vs-th>Icon</vs-th>
                    <vs-th>Name</vs-th>
                    <vs-th>Name Application</vs-th>
                    <vs-th>Action</vs-th>  
                </vs-tr>
                </template>
                <template #tbody>
                <vs-tr
                    :key="i"
                    v-for="(tr, i) in $vs.getPage(dataApplications, page, max)"
                    :data="tr" >
                    <vs-td>
                        <el-image
                        style="width: 100px; height: 100px"
                        :src="tr.url"
                        fit="cover"></el-image>
                    </vs-td>
                    <vs-td>
                    {{ tr.name_categories }}
                    </vs-td>
                    <vs-td>
                      {{ tr.name_applications }}
                    </vs-td>
                    <vs-td>
                        <el-dropdown>
                        <span class="el-dropdown-link">
                            Action<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="detailData(tr.id_categories)" icon="el-icon-check">Detail</el-dropdown-item>
                            <el-dropdown-item @click.native="editData(tr.id_categories)" icon="el-icon-edit">Edit</el-dropdown-item>
                            <el-dropdown-item @click.native="deleteData(tr.id_categories)" icon="el-icon-delete">Delete</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                    </vs-td>
                </vs-tr>
                </template>
                <template #footer>
                <vs-pagination v-model="page" :length="$vs.getLength(dataApplications, max)" />
                </template>
            </vs-table>
        </div>

        </el-card>
    </div>

</template>

<script>


import moduleCategory from '../../store/category/moduleCategory.js'
import router from '@/router'

export default {
    data() {
      return {
          page: 1,
        max: 5,
        
      }
    },
    methods : {
      detailData(idCategory) {
        const id = idCategory
        router.push(`/category/${id}/detail`)
      },
      editData (idCategory) {
        const id = idCategory
        router.push(`/category/${id}/edit`)
      },
      deleteData(dataID) {
        this.$alert('Are you sure you delete this all image in Category', 'Delete!', {
          confirmButtonText: 'OK',
          callback: action => {
            this.dataDel(dataID)
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      dataDel(dataID) {        
        this.$store.dispatch('dataCategory/deleteCategory', dataID)
      },
    },
    computed : {
      dataApplications () {
        return this.$store.state.dataCategory.categories
      },
      baseUrl () {
        const path = this.$route.path
        return path[0]
      },
      headerCard () {
        return this.$route.name
      }
        
    },
    created () {
      if (!moduleCategory.isRegistered) {
        this.$store.registerModule('dataCategory', moduleCategory)
        moduleCategory.isRegistered = true
      }
      const obj = {
        id : this.$store.state.userDataActive.id
      }
      this.$store.dispatch('dataCategory/showCategory', obj)
    }
 
}
</script>

<style>
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
</style>